import React from 'react';
import PropTypes from 'prop-types';

import './index.scss';

const ReactionButton = React.forwardRef((props, ref) => {
  const {
    children,
    width,
    height,
    onClick,
    selected,
    className,
  } = props;

  const injectingClassName = Array.isArray(className) ? className : [className];
  return (
    <div
      ref={ref}
      className={`sendbird-reaction-button${selected ? '--selected' : ''} ${injectingClassName.join(' ')}`}
      style={{
        width: typeof width === 'string' ? `${width.slice(0, -2) - 2}px` : `${width - 2}px`,
        height: typeof height === 'string' ? `${height.slice(0, -2) - 2}px` : `${height - 2}px`,
      }}
      onClick={(e) => onClick(e)}
      role="button"
      onKeyDown={(e) => onClick(e)}
      tabIndex={0}
    >
      <div className="sendbird-reaction-button__inner">
        {children}
      </div>
    </div>
  );
});

ReactionButton.propTypes = {
  children: PropTypes.element.isRequired,
  onClick: PropTypes.func,
  selected: PropTypes.bool,
  width: PropTypes.oneOfType([
    PropTypes.number,
    PropTypes.string,
  ]),
  height: PropTypes.oneOfType([
    PropTypes.number,
    PropTypes.string,
  ]),
  className: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.arrayOf(PropTypes.string),
  ]),
};

ReactionButton.defaultProps = {
  selected: false,
  width: '36px',
  height: '36px',
  onClick: () => { },
  className: '',
};

export default ReactionButton;
